import { API } from "./api"
import { setList, addList as addListAC, setProfileNameAC, setProfileHistoryAC, setProfileListAC, addProfileHistoryAC } from "./actionCreators"

export const getList = (title, type, page) => (dispatch) => {
    if (!title) return
    API.getList(title, type, page)
        .then(data => {
            if (data.Response === 'False') {
                alert(data.Error)
                return
            }
            dispatch(setList(data.Search))
        })
        .then(data => dispatch(addProfileHistoryAC({ title, type })))
        .catch(err => alert('thunks error:', err))
}

export const addList = (title, type, page) => (dispatch) => {
    API.getList(title, type, page)
        .then(data => {
            if (data.Response === 'False') {
                alert(data.Error)
                return
            }
            dispatch(addListAC(data.Search))
        })
        .catch(err => alert('thunks error:', err))
}

export const setProfileName = (name) => (dispatch) => {
    API.setLocalName(name)
        .then(data => dispatch(setProfileNameAC(data)))
        .catch(err => alert('thunks error:', err))
}

export const getProfileName = () => (dispatch) => {
    API.getLocalName()
        .then(data => dispatch(setProfileNameAC(data)))
        .catch(err => alert('thunks error:', err))
}

export const setProfileList = (list) => (dispatch) => {
    API.setLocalList(JSON.stringify(list))
        .catch(err => alert('thunks error:', err))
}

export const getProfileList = () => (dispatch) => {
    API.getLocalList()
        .then(data => {
            if (data) dispatch(setProfileListAC(JSON.parse(data)))
        })
        .catch(err => alert('thunks error:', err))
}

export const clearProfileList = () => (dispatch) => {
    API.clearLocalList()
        .then(data => dispatch(setProfileListAC(data)))
        .catch(err => alert('thunks error:', err))
}

export const setProfileHistory = (history) => (dispatch) => {
    API.setLocalHistory(JSON.stringify(history))
        .catch(err => alert('thunks error:', err))
}

export const getProfileHistory = () => (dispatch) => {
    API.getLocalHistory()
        .then(data => {
            if (data) dispatch(setProfileHistoryAC(JSON.parse(data)))
        }).catch(err => alert('thunks error:', err))
}

export const clearProfileHistory = () => (dispatch) => {
    API.clearLocalHistory()
        .then(data => dispatch(setProfileHistoryAC(data)))
        .catch(err => alert('thunks error:', err))
}