import {
    SET_TITLE, SET_TYPE, SET_PAGE, SET_LIST, ADD_LIST,
    SET_PROFILE_NAME, SET_PROFILE_LIST, ADD_PROFILE_LIST, DELETE_PROFILE_LIST, SET_PROFILE_HISTORY, ADD_PROFILE_HISTORY
} from './actionTypes'

export const setTitle = (title) => ({ type: SET_TITLE, title })
export const setType = (searchType) => ({ type: SET_TYPE, searchType })
export const setPage = (page) => ({ type: SET_PAGE, page })
export const setList = (list) => ({ type: SET_LIST, list })
export const addList = (list) => ({ type: ADD_LIST, list })
export const setProfileNameAC = (name) => ({ type: SET_PROFILE_NAME, name })
export const setProfileListAC = (list) => ({ type: SET_PROFILE_LIST, list })
export const addProfileListAC = (list) => ({ type: ADD_PROFILE_LIST, list })
export const deleteProfileListAC = (imdbID) => ({ type: DELETE_PROFILE_LIST, imdbID })
export const setProfileHistoryAC = (history) => ({ type: SET_PROFILE_HISTORY, history })
export const addProfileHistoryAC = (history) => ({ type: ADD_PROFILE_HISTORY, history })