import React from 'react'
import Container from '../Container/Container';
import Button from '../Button/Button'
import Input from '../Input/Input'
import './SearchSection.scss'

const SearchSection = ({ title, type, list, setTitle, setType, getList }) => {

    return (
        <section className='search'>
            <Container>
                <div className={`search__block ${!list ? 'search__block_middle' : ''}`}>
                    <Input value={title} type='search' onChange={setTitle} />
                    <select className='select' defaultValue={type} onChange={e => setType(e.target.value)}>
                        <option value='movie'>Movie</option>
                        <option value='series'>Series</option>
                    </select>
                    <Button onClick={e => getList(title, type, 1)}>Search</Button>
                </div>
            </Container>
        </section>
    )
}

export default SearchSection