import { connect } from 'react-redux'
import ProfileSection from './ProfileSection'
import { setProfileName, clearProfileHistory, clearProfileList } from '../../state/thunks'

const mapStateToProps = (state) => ({
    name: state.profile.name,
    list: state.profile.list,
    history: state.profile.history
})

export default connect(mapStateToProps, { setProfileName, clearProfileHistory, clearProfileList })(ProfileSection)