import Axios from "axios"

const key = 'dfdae3d4'

const instance = Axios.create({
    baseURL: `https://www.omdbapi.com/`
})

const storage = window.localStorage

export const API = {
    getList(title, type, page) {
        return instance.get(`?apikey=${key}&s=${title}&type=${type}&page=${page}`).then(response => response.data)
    },

    setLocalName(name) {
        return new Promise(function (resolve, reject) {
            storage.setItem('name', name)
            resolve(name)
        })
    },

    getLocalName() {
        return new Promise(function (resolve, reject) {
            let name = storage.getItem('name')
            if (!name) name = 'User'
            resolve(name)
        })
    },

    setLocalList(list) {
        return new Promise(function (resolve, reject) {
            storage.setItem('list', list)
            resolve(list)
        })
    },

    getLocalList() {
        return new Promise(function (resolve, reject) {
            let list = storage.getItem('list')
            resolve(list)
        })
    },

    clearLocalList() {
        return new Promise(function (resolve, reject) {
            storage.setItem('list', '')
            resolve('')
        })
    },

    setLocalHistory(history) {
        return new Promise(function (resolve, reject) {
            storage.setItem('history', history)
            resolve(history)
        })
    },

    getLocalHistory() {
        return new Promise(function (resolve, reject) {
            let history = storage.getItem('history')
            resolve(history)
        })
    },

    clearLocalHistory() {
        return new Promise(function (resolve, reject) {
            storage.setItem('history', '')
            resolve('')
        })
    },
}