import React, { useEffect } from 'react'
import { Route } from 'react-router-dom'
import { connect } from 'react-redux'
import Header from './components/Header/Header'
import SearchSectionContainer from './components/SearchSection/SearchSectionContainer'
import SliderSectionContainer from './components/SliderSection/SliderSectionContainer'
import ProfileSectionContainer from './components/ProfileSection/ProfileSectionContainer'
import { getProfileName, getProfileList, getProfileHistory, setProfileHistory, setProfileList } from './state/thunks'
import logo from './assets/icons/logo.svg'


function App({ getProfileName, getProfileList, getProfileHistory, setProfileList, setProfileHistory, list, history }) {

  useEffect(() => {
    getProfileName()
    getProfileList()
    getProfileHistory()
  }, [getProfileName, getProfileList, getProfileHistory])

  useEffect(() => {
    if (list && list.length) setProfileList(list)
  }, [list, setProfileList])

  useEffect(() => {
    if (history && history.length) setProfileHistory(history)
  }, [history, setProfileHistory])


  return (
    <>
      <Header icon={logo} title='Movie App' />
      <Route exact path='/'>
        <SearchSectionContainer />
        <SliderSectionContainer />
      </Route>
      <Route path='/profile'>
        <ProfileSectionContainer />
      </Route>
    </>
  );
}

const mapStateToProps = (state) => ({
  name: state.profile.name,
  list: state.profile.list,
  history: state.profile.history
})

export default connect(mapStateToProps, { getProfileName, getProfileList, getProfileHistory, setProfileList, setProfileHistory })(App);