import {
    SET_TITLE, SET_TYPE, SET_PAGE, SET_LIST, ADD_LIST,
    SET_PROFILE_NAME, SET_PROFILE_LIST, ADD_PROFILE_LIST, DELETE_PROFILE_LIST, SET_PROFILE_HISTORY, ADD_PROFILE_HISTORY
} from './actionTypes'

const initialState = {
    options: {
        title: '',
        type: 'movie',
        page: 1,
    },
    data: {
        list: null
    },
    profile: {
        name: 'User',
        list: [],
        history: []
    }
}

export const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_TITLE:
            return {
                ...state,
                options: {
                    ...state.options,
                    title: action.title
                }
            }

        case SET_TYPE:
            return {
                ...state,
                options: {
                    ...state.options,
                    type: action.searchType
                }
            }


        case SET_PAGE:
            return {
                ...state,
                options: {
                    ...state.options,
                    page: action.page
                }
            }

        case SET_LIST:
            return {
                ...state,
                data: {
                    ...state.data,
                    list: action.list
                }
            }

        case ADD_LIST:
            return {
                ...state,
                data: {
                    ...state.data,
                    list: [...state.data.list, ...action.list]
                }
            }

        case SET_PROFILE_NAME:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    name: action.name
                }
            }

        case SET_PROFILE_LIST:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    list: action.list
                }
            }

        case ADD_PROFILE_LIST:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    list: [...state.profile.list, action.list]
                }
            }

        case DELETE_PROFILE_LIST:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    list: [...state.profile.list.filter(item => item.imdbID !== action.imdbID)]
                }
            }

        case SET_PROFILE_HISTORY:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    history: action.history
                }
            }

        case ADD_PROFILE_HISTORY:
            return {
                ...state,
                profile: {
                    ...state.profile,
                    history: [...state.profile.history, action.history]
                }
            }

        default:
            return state
    }
}